
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import slide from "@/components/Slide/slide";
import Tabs from "@/components/tabs/tabs";
import SvgProple from "@/components/People/people";
export default {
components: {
XHeader,
Tab,
TabItem,
InlineLoading,
slide,
SvgProple,
Tabs
},
data() {
return {
gotoDetailIndex: 0,
tabIndex: 0,
isloading: true,
isSvgClick: false,
curIndex: 0,
vivus: "delayed",
faces: true,
sexs: true,
durations: 100,
liIndex: -1,
conIndex: 0,
newConIndex: 0,
dataSeeIng: [],
panes: [],
list: {},
firstSet: true,
firstTwoSet: 0, //设置二级获取top 次数
};
},
beforeRouteLeave(to, from, next) {
if (to.name == 'symptomDetails') {
window.localStorage.setItem('gotoSymptomDetails', JSON.stringify({
index: this.gotoDetailIndex,
conIndex: this.newConIndex
}));
} else {
window.localStorage.removeItem('gotoSymptomDetails');
}
next();
},
mounted() {
this.$nextTick(() => {
this.getPanes();
let gotoDetail = JSON.parse(window.localStorage.getItem('gotoSymptomDetails'));
if (gotoDetail) {
this.tabIndex = 1;
this.slide(this.tabIndex);
this.gotoDetailIndex = gotoDetail.index;
this.newConIndex = gotoDetail.conIndex;
}
});
},
updated() {
if (this.$refs.tabasss.$refs.myBoxLi && this.firstSet) {
this.firstSet = false;
this.liIndex = 0;
this.liIndex = this.gotoDetailIndex;
this.conIndex = this.newConIndex
}
},
methods: {

getPanes() {
this.$ajax.post(this.$root.host, {
'MSH.1': 'autodiagnosis',
'MSH.2': 'listPart',
token: this.$root.loginInfo.token,
}).then(data => {
if (typeof data != 'undefined' && data.length > 0) {
for (var index = 0; index < data.length; index++) {
const element = data[index];
this.panes.push({
label: element.CVALUE,
code: element.CBH,
index: index,
})
}
if (this.panes.length > 0) {

let CBH = this.panes[this.gotoDetailIndex].code;
this.getList(this.gotoDetailIndex, CBH);
}
}
})
},

getList(index, CBH) {
let self = this;
if (!this.list[index]) {
this.$ajax.post(this.$root.host, {
'MSH.1': 'autodiagnosis',
'MSH.2': 'listSymptom',
'cbh': CBH,
token: this.$root.loginInfo.token,
}).then(data => {
if (typeof data != 'undefined' && data.length > 0) {
self.list[index] = [];
var arr = [];
for (let i = 0; i < data.length; i++) {
const element = data[i];
var obj = {};
obj["label"] = element.CZZMC;
obj["code"] = element.CBM;
arr.push(obj);
}
self.list[index] = arr;
this.dataSeeIng = this.list[index];
if (this.firstTwoSet < 2) {
this.firstTwoSet++;
this.$refs.tabasss.setScrollPosition();
}
} else {
this.dataSeeIng = [];
}
})
} else {
this.dataSeeIng = this.list[index];
}
},
clickBack(val, group) {
console.log(val);
let alertMess = {
wholeBody: 'DIC181105095150015', //全身
head: 'DIC181105095150003', //头部
neck: 'DIC181105095150004', //颈部
abdomen: 'DIC181105095150005', //腹部(包括胸部)
upperLimb: 'DIC181105095150009', //上肢
waist: 'DIC181105095150007', //腰部
genitals: this.sexs ? 'DIC181105095150012' : 'DIC181105095150013', //男/女性生殖
theLegs: 'DIC181105095150010' 
};
for (let [index, elem] of this.panes.entries()) {
if (elem.code == alertMess[val]) {
console.log(this.$refs.tabasss.$refs);
this.liIndex = index
}
}


this.tabIndex = 1;
this.slide(this.tabIndex);

if (group) {
group.opacity(1);
}
},
slide(index) {
this.$refs.slide.goToPage(index);
},

onLiClick(i, j, obj) {
this.gotoDetailIndex = obj.index;
this.getList(obj.index, obj.code);
},

onConClick(i, j, obj) {
this.newConIndex = j;
this.$router.push({
name: 'symptomDetails',
params: {
CBM: obj.code
}
});
},
animingEnd() {
this.isSvgClick = true;
},
sexControl(state) {
if (this.isSvgClick && this.sexs != state) {
this.isSvgClick = false;
this.sexs = state;
}
}
}
};



